import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import ShowResume from '@/components/ShowResume'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'ShowResume',
      component: ShowResume
    }
  ]
})



